import { useContext, useEffect, useMemo, useState } from "react";
import styled, { css } from "styled-components";
import { ExecutableButton } from "../../../base/components/button/executableButton";
import { _CSS_LABEL_MIDIUM } from "../../../base/design/styles";
import { ChordBacking, DetailChord, DetailFixed, DetailInit, DetailSection, ScoreData } from "../../../redux/store/score/scoreData";
import HarmonizeUtil from "../../../utils/harmonizeUtil";
import { KEY12_MAJOR_SCALE_LIST } from "../../../utils/theoryUtils";
import { GlobalContext } from "../../entry/systemEntry";
import ScoreBuilder from "./scoreBuilder";

//
const getStringFromDate = (date: Date) => {

    const year_str = date.getFullYear().toString();
    //+1
    const month_str = (1 + date.getMonth()).toString();
    const day_str = date.getDate().toString();
    const hour_str = date.getHours().toString();
    const minute_str = date.getMinutes().toString();
    const second_str = date.getSeconds().toString();

    let format_str = 'YYYY-MM-DD hh:mm:ss';
    format_str = format_str.replace(/YYYY/g, year_str);
    format_str = format_str.replace(/MM/g, month_str);
    format_str = format_str.replace(/DD/g, day_str);
    format_str = format_str.replace(/hh/g, hour_str);
    format_str = format_str.replace(/mm/g, minute_str);
    format_str = format_str.replace(/ss/g, second_str);

    return format_str;
};

const ManageTab = () => {

    const { store, dispatcher } = useContext(GlobalContext);

    const [source, setSource] = useState<string>('');
    const [file, setFile] = useState<null | File>(null);

    const fileHandle = store.scoreState.fileHandle;
    const data = store.scoreData;
    const tabIndex = store.systemState.scoreTabIndex;

    const [fileInfo, scoreInfo]: [{
        name: string;
        size: string;
        modified: string;
    }, {
        key: string;
        tempo: string;
        rate: string;
        section: string;
        chord: string;
        bar: string;
        time: string;
        notes: string;
    }] = useMemo(() => {
        let name = '-';
        let size = '-';
        let modified = '-';
        let key = '-';
        let tempo = '-';
        let rate = '-';
        let section = '-';
        let chord = '-';
        let bar = '-';
        let time = '-';
        let notes = '-';
        if (tabIndex === 0) {
            if (file != null) {
                name = file.name;
                size = `${file.size.toString()} byte`;
                modified = getStringFromDate(new Date(file.lastModified));
            }
            if (data.chordList.length > 0) {
                const init = data.chordList[0].detail as DetailInit;
                key = `${KEY12_MAJOR_SCALE_LIST[init.keyIndex]} ${init.scale}`;
                tempo = init.bpm.toString();
                rate = init.beatSignature;
                let sectionNum = 0;
                let chordNum = 0;
                data.chordList.forEach(chord => {
                    switch (chord.type) {
                        case 'section': sectionNum++; break;
                        case 'chord': chordNum++; break;
                    }
                });
                section = sectionNum.toString();
                chord = chordNum.toString();

                const cacheList = store.scoreState.elementCacheList;
                if (cacheList.length > 0) {
                    const last = cacheList[cacheList.length - 1];
                    time = `${last.pastSecond / 1000}s`;
                    bar = `${last.bar} (${last.surplusBeat})`;
                }
                notes = data.notesList.length.toString();
            }
        }
        return [{
            name, size, modified
        }, {
            key, tempo, rate, section, chord, bar, time, notes
        }]
    }, [tabIndex, data, file]);

    useEffect(() => {
        if (tabIndex === 0 && fileHandle != null) {
            fileHandle.getFile().then(file => {
                setFile(file);
            });
        }
    }, [tabIndex]);

    const setData = ((data: ScoreData) => {
        store.scoreState.focusIndex = 2;
        store.scoreState.distIndex = store.scoreState.focusIndex;
        dispatcher.score.setScoreState({ ...store.scoreState });
        // dispatcher.score.setScoreTabIndex(1);
        dispatcher.score.setScoreData(data);
        // 
        store.scoreData = data;
        HarmonizeUtil.updateHarmonizeCache(store, dispatcher);
    });

    return (
        <_Wrap>
            <_Left>
                <_Link onClick={() => {

                    const data: ScoreData = {
                        chordList: [
                            { type: 'init', detail: { keyIndex: 0, bpm: 100, scale: 'major', beatSignature: '4/4' } as DetailInit },
                            { type: 'fixed', detail: { fixed: 'Start' } as DetailFixed },
                            { type: 'section', detail: { labelName: 'Section' } as DetailSection },
                            {
                                type: 'chord', detail: {
                                    beatLen: 4, minute: { head: 0, tail: 0 }, root: null, on: null, symbolKey: 'major',
                                    backing: {
                                        voicingList: [],
                                        pattern: null,
                                        hasError: false
                                    }
                                } as DetailChord
                            },
                            { type: 'fixed', detail: { fixed: 'End' } as DetailFixed },
                        ],
                        notesList: []
                    };
                    // setSource(JSON.stringify(data));
                    setData(data);
                }} >Create an empty score.</_Link>
                <_Link onClick={() => {
                    const builder = new ScoreBuilder({ keyIndex: 0, bpm: 100, scale: 'major', beatSignature: '4/4' });
                    builder.addChordDiatonic(2, 0);
                    builder.addChordDiatonic(2, 4);
                    builder.addChordDiatonic(2, 5);
                    builder.addChordDiatonic(2, 2);
                    builder.addChordDiatonic(2, 3);
                    builder.addChordDiatonic(2, 0);
                    builder.addChordDiatonic(2, 1);
                    builder.addChordDiatonic(2, 4);
                    builder.addChordDiatonic(2, 0);
                    builder.addChordDiatonic(2, 4);
                    builder.addChordDiatonic(2, 5);
                    builder.addChordDiatonic(2, 2);
                    builder.addChordDiatonic(2, 3);
                    builder.addChordDiatonic(2, 0);
                    builder.addChordDiatonic(2, 1);
                    builder.addChordDiatonic(2, 4);
                    builder.addChordDiatonic(2, 0);
                    // setSource(JSON.stringify(builder.getScoreData()));
                    setData(builder.getScoreData());
                }} >mm</_Link>
                <_Link onClick={() => {
                    const builder = new ScoreBuilder({ keyIndex: 0, bpm: 100, scale: 'major', beatSignature: '4/4' });
                    builder.addChordDiatonic(2, 5);
                    builder.addChordDiatonic(2, 3);
                    builder.addChordDiatonic(2, 4);
                    builder.addChordDiatonic(2, 0);
                    builder.addChordDiatonic(2, 5);
                    builder.addChordDiatonic(2, 3);
                    builder.addChordDiatonic(2, 4);
                    builder.addChordDiatonic(2, 0);
                    builder.addChordDiatonic(2, 5);
                    builder.addChordDiatonic(2, 3);
                    builder.addChordDiatonic(2, 4);
                    builder.addChordDiatonic(2, 0);
                    builder.addChordDiatonic(2, 5);
                    builder.addChordDiatonic(2, 3);
                    builder.addChordDiatonic(2, 4);
                    builder.addChordDiatonic(2, 5);
                    // setSource(JSON.stringify(builder.getScoreData()));
                    setData(builder.getScoreData());
                }} >mm</_Link>
                <_Link onClick={() => {
                    const builder = new ScoreBuilder({ keyIndex: 0, bpm: 100, scale: 'major', beatSignature: '4/4' });
                    builder.addChordDiatonic(4, 3);
                    builder.addChordDiatonic(4, 4);
                    builder.addChordDiatonic(4, 2);
                    builder.addChordDiatonic(4, 5);
                    builder.addChordDiatonic(4, 1);
                    builder.addChordDiatonic(4, 4);
                    builder.addChord(4, { index: 0, isFlat: false }, 'suspend4th');
                    builder.addChordDiatonic(4, 0);
                    // setSource(JSON.stringify(builder.getScoreData()));
                    setData(builder.getScoreData());
                }} >mmmsus4</_Link>
            </_Left>
            <_Right>
                {/* <_Source value={source} onChange={e => setSource(e.target.value)} /> */}

                <_InfoDiv>
                    <_FileInfoDiv isEnable={fileHandle != null}>
                        <_TitleLabel>FILE</_TitleLabel>
                        <_Recored>
                            <_ItemnLabel>name</_ItemnLabel>
                            <_ItemnValue>{fileInfo.name}</_ItemnValue>
                        </_Recored>
                        <_Recored>
                            <_ItemnLabel>size</_ItemnLabel>
                            <_ItemnValue>{fileInfo.size}</_ItemnValue>
                        </_Recored>
                        <_Recored>
                            <_ItemnLabel>modified</_ItemnLabel>
                            <_ItemnValue>{fileInfo.modified}</_ItemnValue>
                        </_Recored>
                    </_FileInfoDiv>
                    <_ScoreInfoDiv isEnable={data.chordList.length > 0}>
                        <_TitleLabel>SCORE</_TitleLabel>
                        <_Recored>
                            <_ItemnLabel>key</_ItemnLabel>
                            <_ItemnValue>{scoreInfo.key}</_ItemnValue>
                        </_Recored>
                        <_Recored>
                            <_ItemnLabel>tempo</_ItemnLabel>
                            <_ItemnValue>{scoreInfo.tempo}</_ItemnValue>
                        </_Recored>
                        <_Recored>
                            <_ItemnLabel>rate</_ItemnLabel>
                            <_ItemnValue>{scoreInfo.rate}</_ItemnValue>
                        </_Recored>
                        <_Recored>
                            <_ItemnLabel>section</_ItemnLabel>
                            <_ItemnValue>{scoreInfo.section}</_ItemnValue>
                        </_Recored>
                        <_Recored>
                            <_ItemnLabel>chord</_ItemnLabel>
                            <_ItemnValue>{scoreInfo.chord}</_ItemnValue>
                        </_Recored>
                        <_Recored>
                            <_ItemnLabel>bar</_ItemnLabel>
                            <_ItemnValue>{scoreInfo.bar}</_ItemnValue>
                        </_Recored>
                        <_Recored>
                            <_ItemnLabel>time</_ItemnLabel>
                            <_ItemnValue>{scoreInfo.time}</_ItemnValue>
                        </_Recored>
                        <_Recored>
                            <_ItemnLabel>notes</_ItemnLabel>
                            <_ItemnValue>{scoreInfo.notes}</_ItemnValue>
                        </_Recored>
                    </_ScoreInfoDiv>
                </_InfoDiv>
                <_ButtonsDiv>
                    {/* <_Button onClick={() => {
                        setSource(JSON.stringify(store.scoreData));
                    }}>Reload</_Button>
                    <_Button onClick={() => {
                        const data = JSON.parse(source);
                        dispatcher.score.setScoreData(data);
                        store.scoreState.focusIndex = 2;
                        store.scoreState.distIndex = store.scoreState.focusIndex;
                        dispatcher.score.setScoreState({ ...store.scoreState });

                        dispatcher.score.setScoreTabIndex(1);
                    }}>Apply</_Button> */}

                    <_Button onClick={() => {

                        const options = {
                            types: [
                                {
                                    accept: {
                                        'text/plain': ['.lcrs'],
                                    },
                                },
                            ],
                        };
                        if (fileHandle != null) {

                            (async () => {
                                // FileSystemWritableFileStream 
                                const writable = await fileHandle.createWritable();
                                //
                                await writable.write(JSON.stringify(data));
                                //
                                await writable.close();
                                store.scoreState.fileHandle = fileHandle;
                                dispatcher.score.setScoreState({ ...store.scoreState });
                                const file = await fileHandle.getFile();
                                setFile(file);
                                alert('saved successfully!');
                            })();
                        } else {
                            window.showSaveFilePicker(options).then((handle) => {
                                (async () => {
                                    // FileSystemWritableFileStream 
                                    const writable = await handle.createWritable();
                                    //
                                    await writable.write(JSON.stringify(data));
                                    //
                                    await writable.close();
                                    store.scoreState.fileHandle = handle;
                                    dispatcher.score.setScoreState({ ...store.scoreState });

                                    const file = await handle.getFile();
                                    setFile(file);
                                })();
                            }).catch(() => {
                                console.log('');
                            });
                        }
                    }}>Save</_Button>
                    <_Button onClick={() => {
                        (async () => {
                            const [fileHandle] = await window.showOpenFilePicker();
                            const file = await fileHandle.getFile();
                            const fileContents = await file.text();
                            // console.log(fileContents);
                            store.scoreState.fileHandle = fileHandle;
                            setFile(file);
                            // store.scoreState.focusIndex = 2;
                            // store.scoreState.distIndex = store.scoreState.focusIndex;
                            // dispatcher.score.setScoreState({ ...store.scoreState });
                            // // dispatcher.score.setScoreTabIndex(1);
                            // dispatcher.score.setScoreData();

                            setData(JSON.parse(fileContents));
                        })();
                    }}>Load</_Button>
                    <_Button onClick={() => {
                        store.scoreState.fileHandle = null;
                        dispatcher.score.setScoreState({ ...store.scoreState });
                        // 
                        store.scoreData = data;
                        HarmonizeUtil.updateHarmonizeCache(store, dispatcher);
                        setFile(null);
                    }}>Disconnect</_Button>
                </_ButtonsDiv>
            </_Right>
        </_Wrap>
    );
}

export default ManageTab;

