import { Dispatcher } from "../redux/dispatcher/dispatcher";
import ScoreCache from "../redux/store/score/scoreCache";
import { DetailAlter, DetailChord, DetailFixed, DetailInit, DetailSection } from "../redux/store/score/scoreData";
import { Store } from "../redux/store/store";
import BackingUtil from "./backingUtil";
import { playHarmony } from "./melodyUtil";
import TheoryUtil, { getSymbolFromKey, IntervalRelationProps, KEY12_SHARP_LIST } from "./theoryUtils";

namespace HarmonizeUtil {

    export const previewStart = (store: Store, dispatcher: Dispatcher) => {
        const state = store.scoreState;
        const sampler = store.instruments.sampler;
        const cacheList = state.elementCacheList;
        const chordList = store.scoreData.chordList;
        const reserveTasks = store.reserveTasks;
        let firstTime = -1;
        const update = () => {
            // if (store.invalidate.harmonize) {
            //     store.invalidate.harmonize();
            // }
            dispatcher.score.setScoreState(state);
        }
        // const reserveTasks = store.reserveTasks;
        const init = chordList[0].detail as DetailInit;
        const adjsut = 0;
        for (let i = state.focusIndex; i < chordList.length; i++) {
            const cache = cacheList[i];
            const element = chordList[i];
            if (element.type === 'chord') {
                const detail = element.detail as DetailChord;
                // 
                if (firstTime === -1) {
                    firstTime = cache.pastSecond;
                }


                if (detail.root == null) continue;

                const rootIndex = (detail.root.index + cache.keyIndex) % 12;
                const symbol = getSymbolFromKey(detail.symbolKey);
                const len = detail.beatLen + detail.minute.head + detail.minute.tail;

                const backing = detail.backing;
                const pitchList = BackingUtil.getPitchListFromVoicing(backing.voicingList, rootIndex, symbol);

                const chordTime = 60 / cache.bpm * len;
                reserveTasks.push(
                    setTimeout(() => {
                        // sampler.releaseAll();
                        // stopPiano(store);
                        // store.melodyState.mouseState.curChord = element;
                        // dispatcher.tune.setState(store.melodyState);

                        BackingUtil.playBacking(store, reserveTasks, cache.bpm, backing, pitchList, chordTime);
                        state.focusIndex = i;
                        state.distIndex = state.focusIndex;
                        update();

                    }, cache.pastSecond - firstTime - adjsut)
                );
            }

            if (i === chordList.length - 1) {
                reserveTasks.push(
                    setTimeout(() => {
                        HarmonizeUtil.stopPreview(store, dispatcher, reserveTasks);
                    }, (cache.pastSecond - firstTime - adjsut))
                );
            }
        }
        state.isPreview = true;
        update();
    }

    export const stopPreview = (store: Store, dispatcher: Dispatcher, reserveTasks: NodeJS.Timeout[]) => {
        const state = store.scoreState;
        state.isPreview = false;
        // store.instruments.sampler.releaseAll();
        // state.focusIndex = state.lastFocus;
        reserveTasks.forEach(id => clearTimeout(id));
        dispatcher.system.setReserveTasks([]);
        dispatcher.score.setScoreState(state);

        store.instruments.harmony?.stop();
    }

    export const previewCurHarmony = (store: Store, dispatcher: Dispatcher) => {

        const state = store.scoreState;
        const reserveTasks = store.reserveTasks;
        const cache = state.elementCacheList[state.focusIndex];
        const curChord = store.scoreData.chordList[state.focusIndex];
        if (curChord != null) {

            const detail = curChord.detail as DetailChord;

            if (detail.root == null) return;

            const rootIndex = (detail.root.index + cache.keyIndex) % 12;
            const symbol = getSymbolFromKey(detail.symbolKey);
            const len = detail.beatLen + detail.minute.head + detail.minute.tail;

            const backing = detail.backing;
            const pitchList = BackingUtil.getPitchListFromVoicing(backing.voicingList, rootIndex, symbol);
            const chordTime = 60 / cache.bpm * len;

            BackingUtil.playBacking(store, store.reserveTasks, cache.bpm, backing, pitchList, chordTime);

            reserveTasks.push(setTimeout(() => {
                state.isPreview = false;
                dispatcher.score.setScoreState(state);
            }, chordTime * 1000));
            state.isPreview = true;
            dispatcher.score.setScoreState(state);
        }
    }

    export const updateHarmonizeCache = (store: Store, dispatcher: Dispatcher) => {
        const chordList = store.scoreData.chordList;
        const init = (chordList[0].detail as DetailInit);

        const elementCacheList: ScoreCache.Element[] = [];
        const sectionCacheList: ScoreCache.Section[] = [];
        const beatCacheList: ScoreCache.BeatRange[] = [];
        const scaleCacheList: ScoreCache.KeyScaleRange[] = [];
        const tempoCacheList: ScoreCache.TempoRange[] = [];
        const measureCacheList: ScoreCache.Measure[] = [];

        // 
        let keyIndex = init.keyIndex;
        let scale = init.scale;
        let bpm = init.bpm;

        let totalMinute = 0;
        let sectionNo = -1;
        let sectionBeatSum = 0;
        let pastSecond = 0;
        let curSecond = 0;
        let chordIndex = -1;
        // let curTotalBeat = 0;
        let curMemoriCount = 0;
        let totalBarCount = 0;

        // 16
        let totalMemoriCount = 0;
        let beatRangeMemoriCount = 0;
        let totalBeat = 0;
        let totalMemoriBarCount = 0;

        //
        let beatSignature = init.beatSignature;

        let beatProps = TheoryUtil.getBeatProps(beatSignature);
        chordList.forEach((element, i) => {

            const prevBeat = totalBeat;
            // 
            const prevMemoriCount = totalMemoriCount;
            // 
            const prevBarCount = totalBarCount;
            const prevMinute = totalMinute;
            const type = element.type;

            switch (type) {
                // 
                // case 'fixed': {
                //     const detail = element.detail as DetailFixed;
                //     if (detail.fixed === 'End') {
                //         prevBeat = totalBeat;
                //     }
                // } break;

                // 
                case 'section': {
                    const detail = element.detail as DetailSection;
                    sectionCacheList.push({
                        labelName: detail.labelName,
                        beatSum: 0,
                        rangeIndexStart: i,
                        rangeIndexEnd: chordList.length - 2
                    });
                    sectionNo++;
                    // 21End
                    if (sectionNo > 0) {
                        sectionCacheList[sectionNo - 1].rangeIndexEnd = i - 1;
                        sectionCacheList[sectionNo - 1].beatSum = sectionBeatSum;
                        sectionBeatSum = 0;
                    }
                } break;

                // 
                case 'chord': {
                    pastSecond += curSecond;
                    // curTotalBeat += curSustain;

                    const detail = element.detail as DetailChord;
                    sectionBeatSum += detail.beatLen;

                    const minute = detail.minute.head + detail.minute.tail;
                    totalMinute += minute;
                    chordIndex++;

                    curMemoriCount = detail.beatLen * beatProps.beatMemoriCount + minute;

                    const prevBar = Math.floor(totalBeat / beatProps.barBeatCnt);
                    totalBeat += detail.beatLen;
                    const curBar = Math.floor(totalBeat / beatProps.barBeatCnt);
                    if (prevBar !== curBar) {
                        totalBarCount++
                    }

                    // 
                    curSecond = TheoryUtil.getSustainTime(
                        detail.beatLen,
                        minute,
                        bpm,
                        beatSignature
                    );

                    // 
                    for (let j = 0; j < curMemoriCount; j++) {
                        let rate: ScoreCache.MeasureRate = 16;
                        if (beatRangeMemoriCount % beatProps.beatMemoriCount === 0) rate = 4;
                        else if (beatRangeMemoriCount % (beatProps.beatMemoriCount / (beatProps.beatMemoriCount / 2)) === 0) rate = 8;

                        const barMemoriCount = beatProps.barBeatCnt * beatProps.beatMemoriCount;

                        let dispBarCount = -1;
                        if (beatRangeMemoriCount % barMemoriCount === 0) {
                            // 
                            totalMemoriBarCount++;
                            dispBarCount = totalMemoriBarCount;
                        }
                        measureCacheList.push({ rate, barCount: dispBarCount });
                        totalMemoriCount++;
                        beatRangeMemoriCount++;
                    }
                } break;

                // 
                case 'alter': {
                    const detail = element.detail as DetailAlter;
                    switch (detail.alterType) {
                        // 
                        case 'modulate': {
                            scaleCacheList.push({
                                keyIndex,
                                scale,
                                length: 1
                            });
                            switch (detail.modRel) {
                                case 'domm': {
                                    keyIndex = (keyIndex + 24 + 7 * detail.dommVal) % 12;
                                } break;
                                case 'key': {
                                    keyIndex = (keyIndex + 12 + detail.keyAdj) % 12;
                                } break;
                                case 'parallel': {
                                    keyIndex = (keyIndex + 12 + (scale === 'major' ? -1 : 1) * 3) % 12;
                                    // 
                                    scale = scale === 'major' ? 'minor' : 'major';
                                } break;
                                case 'relative': {
                                    // 
                                    scale = scale === 'major' ? 'minor' : 'major';
                                } break;
                            }
                        } break;
                        // 
                        case 'tempo': {
                            switch (detail.tempoRel) {
                                case 'diff': {
                                    bpm += detail.tempoDiff;
                                } break;
                                case 'rate': {
                                    bpm = Math.floor(bpm * 1 * (detail.tempoRate / 100));
                                } break;
                                case 'abs': {
                                    bpm = detail.tempoAbs;
                                } break;
                            }
                        } break;
                        // 
                        case 'beat': {
                            // const prevBeatPos = beatCacheList.map(cache => cache.length).reduce((total, len) => total + len, 0);
                            beatCacheList.push({
                                beatSignature,
                                length: beatRangeMemoriCount
                            });

                            beatSignature = detail.beatSignature;
                            // 0
                            totalBeat = 0;
                            beatRangeMemoriCount = 0;

                            beatProps = TheoryUtil.getBeatProps(beatSignature);
                        } break;
                    }
                }
                    break;
            }
            // const pastSecond = 60000 / init.bpm * (prevBeat + prevMinute);
            elementCacheList.push({
                bar: prevBarCount,
                surplusBeat: prevBeat % beatProps.barBeatCnt,
                minute: prevMinute,
                keyIndex,
                scale,
                bpm,
                beatSignature,
                sectionNo,
                pastSecond,
                chordIndex,
                curTotalMemori: prevMemoriCount,
                reffers: {}
            });
        });
        sectionCacheList[sectionNo].beatSum = sectionBeatSum;

        // const prevBeatPos = beatCacheList.map(cache => cache.length).reduce((total, len) => total + len, 0);
        beatCacheList.push({
            beatSignature,
            length: beatRangeMemoriCount
        });

        store.scoreState.elementCacheList = elementCacheList;
        store.scoreState.sectionCacheList = sectionCacheList;
        store.scoreState.beatCacheList = beatCacheList;
        store.scoreState.measureCacheList = measureCacheList;
        dispatcher.score.setScoreState(store.scoreState);
    }
}

export default HarmonizeUtil;
